<template>
    <canvas ref="canvas"></canvas>
</template>

<script>
export default {
    name:"lineChart",
    data(){
        return{
            lineColor:["#f04f4f","#24b7e7","#7064b7"],
            lineTip:["学生身份","教师身份","游客身份"],
            lineData:[
                [{
                    time:'09-01',
                    num:0
                },{
                    time:'09-02',
                    num:4
                },{
                    time:'09-03',
                    num:8
                },{
                    time:'09-04',
                    num:13
                },{
                    time:'09-05',
                    num:6
                },{
                    time:'09-06',
                    num:13
                },{
                    time:'09-07',
                    num:6
                }],
                [{
                    time:'09-01',
                    num:0
                },{
                    time:'09-02',
                    num:15
                },{
                    time:'09-03',
                    num:16
                },{
                    time:'09-04',
                    num:10
                },{
                    time:'09-05',
                    num:4
                },{
                    time:'09-06',
                    num:10
                },{
                    time:'09-07',
                    num:4
                }],
                [{
                    time:'09-01',
                    num:0
                },{
                    time:'09-02',
                    num:7
                },{
                    time:'09-03',
                    num:3
                },{
                    time:'09-04',
                    num:12
                },{
                    time:'09-05',
                    num:9
                },{
                    time:'09-06',
                    num:10
                },{
                    time:'09-07',
                    num:4
                }]
            ],
            axisNum:7,
            axisLimit:20
        }
    },
    props:["chooseTitle","topLength"],
    methods:{
        // 绘制文字
        drawText(c,x,y,text,color,align='left',size="18px"){
            c.fillStyle=color;
            c.beginPath();
            c.textAlign = align;
            c.textBaseline = 'middle';
            c.font = `${size} Adobe Ming Std`;
            c.closePath();
            c.fillText(text, x,y);
        },
        // 绘制小圆圈
        drawCircle(c,x,y,l=3,color='#999'){
            c.strokeStyle=color;
            c.fillStyle='#fff';
            c.beginPath();
            c.arc(x,y,l,0,Math.PI*2);
            c.closePath();
            c.fill();
            c.stroke();
        },
        // 绘制颜色曲线提示
        drawTip(c,x,y,text,color){
            let width=36;
            let height=2;
            let margin=10;
            c.beginPath();
            c.fillStyle=color;
            c.rect(x-width/2,y-height/2,width,height);
            c.closePath();
            c.fill();
            this.drawCircle(c,x,y,8,'#999');
            this.drawText(c,x+margin+width/2,y-height/2,text,'#999');
        },
        // 绘制统计图底图 x,y为原点坐标
        drawChart(c,x,y,width,height,divide){
            let axisColor="#999";
            let lineColor="#dbdbdb";
            c.beginPath();
            c.strokeStyle=axisColor;
            c.moveTo(x,y);
            c.lineTo(x+width,y);
            c.closePath();
            c.stroke();
            c.beginPath();
            c.moveTo(x,y);
            c.lineTo(x,y-height);
            c.closePath();
            c.stroke();
            for(let i=0;i<=divide;i++){
                c.beginPath();
                c.strokeStyle="#ccc";
                c.moveTo(x,y-i*height/divide);
                c.lineTo(x+width,y-i*height/divide);
                c.closePath();
                c.stroke();
                this.drawCircle(c,x,y-i*height/divide);
                this.drawText(c,x-10,y-i*height/divide,i*this.axisLimit/divide,'#2c3242','right');
            }
            this.lineData.forEach((ld,index)=>{
                this.drawDataLine(c,x,y,this.lineData[index],this.lineColor[index]);
            })
            for(let i=0;i<=this.axisNum;i++){
                this.drawCircle(c,x+i*width/(this.axisNum-1),y);
            }
            
        },
        // 绘制折线
        drawDataLine(c,x,y,data,color,width=1400,height=240){
            c.beginPath();
            c.moveTo(x,y);
            data.forEach((d,index)=>{
                c.strokeStyle=color;
                c.lineTo(x+index*width/(this.axisNum-1),y-height/this.axisLimit*d.num);
            })
            c.stroke();
            data.forEach((d,index)=>{
                this.drawText(c,x+index*width/(this.axisNum-1),y+20,d.time,"#2c3242",'center','14px');
            })
            c.closePath();
            data.forEach((d,index)=>{
                this.drawCircle(c,x+index*width/(this.axisNum-1),y-height/this.axisLimit*d.num);
            })
        }
    },
    mounted(){
        let canvas=this.$refs.canvas;
        canvas.width=1500;
        canvas.height=this.topLength+30;
        let context =canvas.getContext('2d');
        let title="";
        if(this.chooseTitle=="流量分析"){
            title="流量统计";
        }else if(this.chooseTitle=="用户分析"){
            title="用户统计"
        }else if(this.chooseTitle=="后台首页"){
            title="访问统计"
        }
        this.drawText(context,10,10,title,"#2c3242");
        let perMargin=140;
        this.lineColor.forEach((color,index)=>{
            this.drawTip(context,1100+perMargin*index,20,this.lineTip[index],color);
        })
        this.drawChart(context,82,this.topLength,1400,240,5);
    }
}
</script>

